@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600,700);
* {
  box-sizing: border-box; }

body, html {
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
  font-weight: normal;
  margin: 0;
  padding: 0;
  background-color: #f1f1f1; }

ul, li {
  margin: 0;
  padding: 0; }

h1, .title-h1 {
  font-size: 50pt;
  line-height: 67pt;
  font-weight: 700;
  margin: 0;
  width: 100%; }

h2, .title-h2 {
  font-size: 38pt;
  line-height: 40pt;
  font-weight: 600;
  margin: 0; }

h4, .title-h4 {
  font-size: 18pt;
  line-height: 22pt;
  font-weight: 600;
  margin: 0; }

.sayer {
  width: 640px;
  margin: 0 auto;
  background-color: #fff;
  box-shadow: 0 0 9px 0px #aaa;
  min-height: 100vh; }
  .sayer-header {
    background-color: #0E1D3C;
    color: #fff;
    padding: 37px 44px 63px;
    flex-wrap: wrap;
    display: flex;
    align-items: center; }

.empty-block {
  font-size: 20pt;
  padding: 28px;
  height: calc(100vh - 320px); }

.header-back {
  background: url(/test-app/static/media/back.bdec7164.svg);
  background-size: 71px;
  width: 71px;
  height: 71px;
  display: inline-block;
  position: relative;
  transition: left .2s;
  left: -11px; }
  .header-back:hover {
    left: -17px; }

.title-page {
  width: calc(100% - 72px);
  font-size: 28pt;
  line-height: 32pt;
  padding-left: 19px; }

.topics-remove {
  color: #fff;
  background-color: #D4145A;
  max-width: 193px;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  align-items: center;
  padding: 0 38px 0 46px;
  font-weight: 400;
  -webkit-transform: translateX(195px);
          transform: translateX(195px);
  transition: background-color .2s, -webkit-transform .2s;
  transition: transform .2s, background-color .2s;
  transition: transform .2s, background-color .2s, -webkit-transform .2s;
  cursor: pointer; }
  .topics-remove:hover {
    background-color: #ee4784; }

.topics-item {
  font-size: 27pt;
  color: #2B2F3E;
  overflow: hidden;
  position: relative;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 22px 18px 23px 35px;
  border-bottom: 1px solid #CCCCCC;
  height: 125px;
  transition: background-color .2s; }
  .topics-item:first-child {
    padding-top: 39px;
    height: 142px; }
  .topics-item:hover {
    background-color: #f5f5f5; }
    .topics-item:hover .topics-remove {
      -webkit-transform: translateX(0);
              transform: translateX(0); }
  .topics-item a {
    color: #2B2F3E;
    width: 80%; }

.topics-title {
  display: inline-block;
  max-width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden; }

.topics-counter {
  background-color: #313464;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  width: 72px;
  height: 72px;
  position: relative;
  top: -4px; }

.create-item {
  padding: 45px 0;
  display: flex;
  justify-content: center; }
  .create-item-button {
    background-color: #D4145A;
    border: 0;
    outline: none;
    border-radius: 100%;
    font-size: 72pt;
    width: 112px;
    height: 112px;
    position: relative;
    box-shadow: 2px 2px 10px 4px rgba(0, 0, 0, 0.3);
    transition: box-shadow .2s;
    cursor: pointer; }
    .create-item-button:hover {
      box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.1); }
  .create-item-text {
    font-size: 69pt;
    line-height: 72pt;
    color: #F5A8F0;
    position: absolute;
    top: -2px;
    left: 27px;
    font-weight: 600; }

.topics-container {
  max-height: calc(100vh - 451px);
  min-height: 400px;
  overflow-y: auto; }

.new-item-form {
  height: 80px;
  padding: 10px 12px 0px 31px;
  display: flex;
  align-items: center;
  margin-top: 35px; }

.new-item-input {
  border: 0;
  border-bottom: 1px solid #333;
  border-radius: 0;
  font-size: 26pt;
  height: 100%;
  vertical-align: top;
  padding: 0 25px;
  width: calc(100% - 112px);
  color: #333;
  outline: none; }

.new-item-button {
  background: url(/test-app/static/media/send.cc145e25.svg);
  background-size: 71px;
  width: 71px;
  height: 71px;
  display: inline-block;
  position: relative;
  transition: left .2s;
  left: 0;
  border: 0;
  outline: none;
  cursor: pointer;
  margin-left: 26px; }
  .new-item-button:hover {
    left: 5px; }

.comments-item {
  display: flex;
  align-items: flex-start;
  padding: 28px 28px 59px 28px;
  border-bottom: 1px solid grey; }
  .comments-item:last-child {
    border: 0; }

.comments-avatar {
  min-width: 100px;
  min-height: 100px; }
  .comments-avatar.male {
    background-color: #313464; }
  .comments-avatar.female {
    background-color: #AF7D93; }

.comments-text {
  padding-left: 44px;
  font-size: 21pt;
  font-family: 'Arial', 'Open Sans', sans-serif;
  line-height: 30pt;
  word-break: break-all; }

.sayer-header {
  padding: 61px 58px 73px 44px; }

.comments {
  padding-top: 17px; }
  .comments-scroll {
    overflow-y: auto;
    max-height: calc(100vh - 320px);
    height: calc(100vh - 320px); }

.new-comment-form {
  height: 110px;
  background-color: #E6E6E6;
  padding: 12px;
  display: flex;
  align-items: center; }

.new-comment-input {
  border: 1px solid #ccc;
  border-radius: 3px;
  font-size: 26pt;
  height: 100%;
  vertical-align: top;
  padding: 0 25px;
  width: calc(100% - 120px);
  color: #333; }

.new-comment-button {
  background: url(/test-app/static/media/send.cc145e25.svg);
  background-size: 71px;
  width: 71px;
  height: 71px;
  display: inline-block;
  position: relative;
  transition: left .2s;
  left: 0;
  border: 0;
  outline: none;
  cursor: pointer;
  margin-left: 34px; }
  .new-comment-button:hover {
    left: 5px; }

